let updating = false;
export default {
	name: 'hj-dragabledrawer',
	props: {
		/**
		 * 显示状态
		 */
		options: Object
	},
	data() {
		return {
			def: {
				rightMode: true,
				mask: true,
				width: 60,
				autoClose: true
			},





			animationData: {},

			show: true,
			visible: false
		}
	},
	computed: {
		option: function() {
			return { ...this.def,
				...this.options
			}
		},
		showmask: function() {
			return this.option.mask && this.show
		}
	},
	created() {
		try {











			this.animation = uni.createAnimation();

			this.screenWidth = uni.getSystemInfoSync().windowWidth;





		} catch (e) {
			this.$emit("error", e)
		}
	},
	methods: {
		open() {
			this.visible = true;
			this.$emit("open", {
				success: 1
			})
		},
		close(force = 1) {
			if (force || this.option.autoClose) {
				this.visible = false;









				this.$emit("close", {
					success: 1
				})

			}
		},
		updateStart: function(e) {
			this.start = e.touches[0].pageX;
			this.scale = this.screenWidth / (this.screenWidth - this.start)
		},
		async updateMove(e) {
			if (updating) return
			updating = true;

			this.stopref()

			this.tx = this.scale * (e.touches[0].pageX - this.start);
			this.show = false;

			if (this.option.rightMode) {
				if (this.tx > 0) {
					this.animation.translateX(this.tx).step({
						duration: 100
					})
				}
			} else {
				if (this.tx < 0) {
					this.animation.translateX(this.tx).step({
						duration: 100
					})
				}
			}
			this.animationData = this.animation.export()













			updating = false
		},




























		updateEnd: function() {
			this.show = true;

			this.stopref();


			if (this.option.rightMode) {
				if (this.tx > 100) {
					this.close()
				}
			} else {
				if (this.tx < -80) {
					this.close()
				}
			}
			this.animation.translateX(0).step();
			this.animationData = this.animation.export();
















			this.tx=0
		},
		stopref: uni.stopPullDownRefresh
	}
}
